import Vue from 'vue'
import Router from 'vue-router'
const _import = require('./_import_' + process.env.NODE_ENV)
// in development-env not use lazy-loading, because lazy-loading too many pages will cause webpack hot update too slow. so only in production use lazy-loading;
// detail: https://panjiachen.github.io/vue-element-admin-site/#/lazy-loading

Vue.use(Router)

/* Layout */
import Layout from '../views/layout/Layout'

/**
* hidden: true                   if `hidden:true` will not show in the sidebar(default is false)
* redirect: noredirect           if `redirect:noredirect` will no redirct in the breadcrumb
* name:'router-name'             the name is used by <keep-alive> (must set!!!)
* meta : {
    title: 'title'               the name show in submenu and breadcrumb (recommend set)
    icon: 'svg-name'             the icon show in the sidebar,
  }
**/
export const constantRouterMap = [
  { path: '/login', component: _import('login/index'), hidden: true },
  { path: '/404', component: _import('404'), hidden: true },

  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    name: 'Dashboard',
    hidden: true,
    children: [{
      path: 'dashboard',
      component: _import('dashboard/index')
    }]
  }
]

export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = [
  {
    path: '/ssl',
    component: Layout,
    redirect: 'ssl/index',
    name: '证书管理',
    noDropdown: true,
    children: [{ path: 'index', component: _import('ssl/index'), name: '证书管理页', meta: { title: '证书管理', icon: 'ssl', perm: ['1'] }}]
  },
  {
    path: '/nginx',
    component: Layout,
    redirect: 'nginx/index',
    name: 'nginx管理',
    noDropdown: true,
    children: [{ path: 'index', component: _import('nginx/index'), name: 'nginx管理页', meta: { title: '服务器管理', icon: 'server', perm: ['1'] }}]
  },
  {
    path: '/domain',
    component: Layout,
    redirect: 'domain/index',
    name: 'domain管理',
    noDropdown: true,
    children: [{ path: 'index', component: _import('domain/index'), name: 'domain管理页', meta: { title: '域名管理', icon: 'domain', perm: ['1'] }}]
  },
  // {
  //   path: '/upstream',
  //   component: Layout,
  //   redirect: 'upstream/index',
  //   name: 'upstream管理',
  //   noDropdown: true,
  //   children: [{ path: 'index', component: _import('upstream/index'), name: 'upstream管理页', meta: { title: 'upstream', icon: 'server', perm: ['4'] }}]
  // },
  {
    path: '/users',
    component: Layout,
    redirect: 'users/index',
    name: 'users管理',
    noDropdown: true,
    children: [{ path: 'user', component: _import('users/user'), name: 'users管理页', meta: { title: '用户管理', icon: 'user', perm: ['1'] }}]
  },
  { path: '*', redirect: '/404', hidden: true }

]



